@import "./index.scss";

.pokecard {
  justify-content: center;
  align-items: center;
  width: 200px;
  padding: 10px;
  margin: 10px;
  height: fit-content;
  background: lighten($medblue, 45%);
  color: $darkblue;
  border-bottom: 5px solid $hellayellow;
  position: relative;
  box-shadow: 1px 1px 5px rgb(119, 113, 113);
}
.pokecard:hover,
.pokecard:focus {
  background: lighten($medblue, 58%);
  border-bottom-color: $salmon;
  .type-list {
    position: relative;
    bottom: 30px;
    z-index: 5;
  }
}

.pokename {
  color: darken($lightblue, 85%);
  font-size: 24px;
  text-align: center;
  margin: 0 0 5px 0;
  font-family: $heading-font-family;
  text-transform: capitalize;
  font-family: $heading-font-family;
}

.typelist {
  font-size: 13px;
  -webkit-column-count: 2;
  -moz-column-count: 2;
  column-count: 2;
  margin: 0 5px 0 0;
  text-transform: uppercase;
  position: relative;
}
